.product-card {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;

  &__link {
    display: flex;
    margin-bottom: 16px;
  }

  &__images {
    border-radius: 8px;
    width: 100%;
    display: flex;
    overflow: hidden;
  }

  &__img {
    width: 100%;
    object-fit: cover;

    @include onDesktop {
      height: 560px;
    }

    @include hover(transform, scale(1.03));
  }

  &__title {
    @extend %uppercase;
    color: $c-grey;
    margin-bottom: 8px;
  }

  &__description {
    margin-bottom: 16px;
  }

  &__button {
    width: max-content;
    display: flex;
    align-items: center;
    height: 32px;
    gap: 8px;

    @extend %uppercase;
    fill: $c-accent-orange;
    color: $c-accent-orange;

    transition: all $effectDuration;

    &:hover svg {
      transform: rotate(360deg);
      transition: $effectDuration;
    }
  }
}