.footer {
  background-color: $c-black;
  padding-block: 56px;

  @include onTablet {
    padding-block: 88px;
  }


  &__content {
    @include pageGrid;
    row-gap: 64px;
    padding-block-end: 40px;
  }


  &__section-nav {
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: span 4;
    }
  }

  &__section-contacts {
    grid-column: 1 / -1;
    display: flex;
    flex-direction: column;
    gap: 40px;

    @include onTablet {
      grid-column: span 4;
    }

    @include onLaptop {
      display: grid;
      column-gap: 24px;
      grid-template-columns: repeat(4, 1fr);
    }
  }

  &__title {
    grid-column: span 4;
  }

  &__contacts {
    display: flex;
    flex-direction: column;
    gap: 16px;

    &--grid--5-7 {
      grid-column: span 2;
    }

    &--grid--7-9 {
      grid-column: span 2;
    }
  }

  &__logo {
    margin-bottom: 40px;

    @include onTablet {
      margin-bottom: 96px;
    }

    @media (min-width: 806px) {
      margin-bottom: 48px;
    }

    @include onLaptop {
      margin-bottom: 61px;
    }
  }

  &__subtitle {
    @extend %uppercase;
    color: $c-grey;
  }

  &__contact {
    color: $c-white;
    transition: color $effectDuration;

    &:hover {
      color: $c-accent-orange;
    }
  }

  &__bottom {
    display: flex;
    flex-direction: column;
    gap: 40px;

    @include onTablet {
      @include pageGrid;
    }
  }

  &__social {
    display: flex;
    gap: 32px;

    @include onTablet {
      grid-column: span 4;
    }

    &-icon {
      fill: $c-accent-orange;
      transition: fill $effectDuration;

      &:hover {
        fill: $c-white;
      }
    }
  }

  &__copyright {
    color: $c-grey;

    @include onTablet {
      grid-column: span 4;
    }
  }
}