.products {
  padding-block-start: 64px;

  @include onTablet {
    padding-block-start: 228px;
  }

  @include onLaptop {
    padding-block-start: 218px;
  }

  @include onDesktop {
    padding-block-start: 178px;
  }

  &__title {
    margin-bottom: 24px;
  }

  &__description {
    margin-bottom: 56px;

    @include onTablet {
      margin-bottom: 80px;
    }
  }

  &__cards-container {
    display: grid; 
    row-gap: 64px;

    @include onTablet {
      row-gap: 88px;
    }
  }

  &__cards {
    display: flex;
    flex-direction: column;
    gap: 64px;

    @include onTablet {
      @include pageGrid;
    }
  }

  &__card {
    @include onTablet {
      grid-column: span 4;
    }

    @include onLaptop {
      grid-column: span 3;
    }

    &--grid--tablet--3-6 {
      @include onLaptop {
        grid-column: 3 / 6;
      }
    }

    &--grid--tablet--6-9 {
      @include onLaptop {
        grid-column: 6 / 9;
      }
    }
  }
}