.nav {
  &__list {
    display: flex;
    gap: 64px;

    &--menu {
      display: flex;
      flex-direction: column;
      gap: 24px;
      align-items: center;
    }

    &--footer {
      flex-direction: column;
      gap: 16px;
    }
  }

  &__link {
    position: relative;
    display: flex;
    @extend %body-text;
    color: $c-black;
    transition: color $effectDuration;

    &::after {
      content: '';
      display: block;
      position: absolute;
      bottom: -7px;
      height: 1px;
      width: 100%;
      background-color: $c-accent-orange;

      transition: transform $effectDuration;
      transform: scale(0);
      transform-origin: left;
    }

    &:hover::after {
      transform: scale(1);
    }

    &:hover {
      color: $c-accent-orange;
    }

    &--menu {
      text-align: center;
      width: fit-content;
    }

    &--footer {
      color: $c-white;
      width: fit-content;
    }
  }

  &__link--contact-us {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 7px;
    color: $c-accent-orange;
    @extend %uppercase;
  }
}