.page {
  font-family: Inter, san-serif;
  color: $c-black;
  background-color: $c-light-blue;
  @extend %body-text;

  scroll-behavior: smooth;

  &__body {
    min-width: 320px;

    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    transition: all 0.3s;
    opacity: 0;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
}