.about-us {
  padding-block: 72px;

  @include onTablet {
    padding-block: 152px;
  }

  @include onLaptop {
    padding-block: 178px 56px;
  }


  &__content {
    background-image: url("../images/about-us/about-us-bg.png");
    background-repeat: no-repeat;
    background-position-y: 75%;
    background-size: contain;
    display: grid;
    row-gap: 64px;

    @include onTablet {
      background-position: center;
      row-gap: 72px;
    }

    @include onLaptop {
      padding-block-end: 293px;
      background-position-x: 75%;
      row-gap: 144px;
    }

    @include onDesktop {
      background-position-x: center;
    }
  }

  &__top {
    @include pageGrid;
    row-gap: 32px;
  }

  &__title {
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: span 4;
    }
  }

  &__description {
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: span 4;
    }
  }

  &__progress {
    @include pageGrid;
    row-gap: 48px;

    @include onTablet {
      row-gap: 80px;
    }
  }

  &__progress-card {
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: span 4;
    }

    @include onLaptop {
      grid-column: span 2;
    }
  }
}