.header {
  &__nav {
    display: none;

    @include onLaptop {
      display: block;
    }
  }

  &__bottom {
    padding: 32px 0 40px;

    @include onTablet {
      @include pageGrid;
      padding: 80px 0 0;
    }

    @include onLaptop {
      background-image: url("../images/header/header-laptop.png");
      background-repeat: no-repeat;
      background-position: center;
      padding: 144px 0 95px;
    }

    @include onDesktop {
      background-image: url("../images/header/header-desktop.png");
      background-repeat: no-repeat;
      background-position: center;
      padding: 135px 0 112px;
    }
  }

  &__description {
    grid-column: 1 / -1;
    margin-bottom: 32px;
    text-align: center;

    @include onTablet {
      max-width: 280px;
      grid-column: 1 / 5;
      text-align: initial;
      margin-bottom: 16px;
    }

    @include onLaptop {
      width: 100%;
      grid-column: span 2;
      margin-bottom: 8px;
      max-width: 260px;
    }

    @include onDesktop {
      width: 100%;
      grid-column: span 2;
      margin-bottom: 40px;
      max-width: 100%;
    }
  }

  &__title {
    grid-column: 1 / -1;
    @extend %h1;
    color: $c-black;
    text-align: center;

    @include onTablet {
      padding: 130px 0;
      margin-bottom: 80px;
      background-image: url("../images/header/header-tablet.png");
      background-image: cover;
      background-repeat: no-repeat;
      background-position: center;
    }

    @include onLaptop {
      background-image: none;
      padding: 0;
      margin-bottom: 64px;
    }
  }

  &__img-box {
    margin: 0 auto;
    max-width: 328px;

    @include onTablet {
      display: none;
    }
  }

  &__img {
    display: block;
    width: 100%;
    object-fit: cover;
    object-position: center;
  }

  &__button {
    // grid-column: 1 / -1;
    max-width: 288px;
    margin: 0 auto;

    @include onTablet {
      grid-column: 3 / 7;
    }

    @include onLaptop {
      grid-column: 1 / 3;
    }
  }
}