@mixin onTablet {
  @media (min-width: $tablet-min-width) {
    @content;
  }
}

@mixin onLaptop {
  @media (min-width: $laptop-min-width) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: $desktop-min-width) {
    @content;
  }
}

@mixin contentPaddingInline() {
  padding-inline: 16px;

  @include onTablet {
    padding-inline: 44px;
  }

  @include onDesktop {
    max-width: 1192px;
    margin-inline: auto;
    padding-inline: 124px;
  }
}

@mixin hover($property, $toValue) {
  transition: #{$property} $effectDuration;

  &:hover {
    #{$property}: $toValue;
  }
}

@mixin pageGrid {
  --colums: 2;
  display: grid;
  column-gap: 16px;
  grid-template-columns: repeat(var(--colums), 1fr);

  @include onTablet {
    --colums: 8;
    column-gap: 24px;
  }
}