.button {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 4px;
  height: 56px;
  width: 100%;

  background-color: transparent;
  border: 2px solid $c-accent-orange;
  border-radius: 32px;

  @extend %uppercase;
  color: $c-accent-orange;
  fill: $c-accent-orange;
  transition: all $effectDuration;

  &:hover {
    color: $c-white;
    fill: $c-white;
    background-color: $c-accent-orange;
  }

  &:active {
    background-color: $c-dark-orange;
    border-color: $c-dark-orange;
  }
}